/**
 * Returns a random integer between 100 and 1000.
 * @customfunction
 * @param {string} subjectRef Cell reference for subject
 * @param {string} metricRef Cell reference for metric
 * @param {string} scenarioRef Cell reference for scenario
 * @param {string} periodRef Cell reference for period
 * @returns {number}
 */
 function PULL(subjectRef, metricRef, scenarioRef, periodRef) {
  return Math.floor(Math.random() * 901) + 100;
}
CustomFunctions.associate("PULL", PULL);

CustomFunctions.associate("PULL", PULL);